import { h } from '@stencil/core';
import {
  MtEspecialidadesEnum,
  MtProdutosEnum,
} from '../../../../global/typescript/enums';

export const markdown = (
  produto?: string,
  especialidade?: string,
  _apostila?: string,
  _parte?: string,
  _ano?: string,
) => {
  if (produto === MtProdutosEnum.MEDCURSO) {
    if (especialidade === MtEspecialidadesEnum.NEF) {
      return (
        <div class="mt-quadro-04__container">
          <div class="mt-quadro-04__left">
            <slot name="left"></slot>
          </div>
          <div class="mt-quadro-04__inner-container">
            <p class="mt-quadro-04__text">
              <slot></slot>
            </p>
          </div>
        </div>
      );
    }
  }

  return (
    <p>
      <slot></slot>
    </p>
  );
};
